import React, { useEffect, useState } from 'react';
import axios  from 'axios';
import { Layout } from '../components/Layout'
import { card } from './styles/PostStyles';

const BASE_URL ='https://simple-blog-api.crew.red/posts'

export default function Posts() {
    const [ posts, setPosts ] = useState ([]);

    useEffect(() => {
        axios.get(BASE_URL)
            .then(function (response) {
                const postList = response.data.reverse();
                setPosts(postList);
            })
            .catch(function (error) {
                console.log(error);
            })
            .finally(function () {

            });
    }, []);

    const handleClickEdit = (id) => {
        console.log(id)
    };

    const handleClickDelete = (id) => {
        axios.delete(`${BASE_URL}/${id}`);
        setPosts(posts.filter(post => post.id !== id));
    };

    return (
        <Layout>
            <card.List>
                {posts.map(post => (
                    <card.Item key={`${post.id}-${post.title} `}>
                        <card.ItemWrapper >
                            <div>
                                <card.Title>
                                    <a title={post.title}>{post.title}</a>
                                </card.Title>
                                <button type="button" onClick={handleClickEdit}>
                                    Edit
                                </button>
                                <button type="button" onClick={() => handleClickDelete(post.id)}>
                                    Delete
                                </button>
                            </div>
                            <card.PostBody>{post.body}</card.PostBody>
                        </card.ItemWrapper>
                    </card.Item>
                ))}
            </card.List>
        </Layout>
    );
}